@use '../stylesVar/colors.scss' as colors;

.secondary-button {
	padding: 1.2rem 2.4rem;
	background: colors.$secondary-button-color;
	border-radius: 0.8rem;
	border: 1px solid colors.$primary-green;
	a {
        display: flex;
        flex-direction: row;
        justify-content: center;
        padding: 1px;
		font-size: 1.6rem;
		font-style: normal;
		font-weight: 600;
		line-height: 2rem;
		color: colors.$primary-text-color;
		text-decoration: none;
	}
}
.button__icon {
    height: 14px;
    width: 16px;
    margin-top: 1px;
	margin-right: 8px;
}
