@use '../stylesVar/colors.scss' as colors;

.primary-button {
	padding: 1.2rem 2.4rem;
	background: colors.$primary-button-color;
	border-radius: 0.8rem;
	border: none;
	a {
        font-size: 1.6rem;
		font-style: normal;
		font-weight: 600;
		line-height: 2rem;
		color: colors.$text-button-color;
        text-decoration: none;
	}
}
